/*Calendar icon*/
.date {
  display: block;
  width: 50px;
  height: 75px;
  margin: 30px auto;
  margin-top:15px;
  background: #fff;
  text-align: center;
  font-family: 'Helvetica', sans-serif;
  position: relative;
  margin-right: 20px;
  border-radius: 2px;
}

.date .binds {
  position: absolute;
  height: 15px;
  width: 20px;
  background: transparent;
  border: 2px solid #999;
  border-width: 0 5px;
  top: -6px;
  left: 0;
  right: 0;
  margin: auto;
}

.date .month {
  background: #fa565a;
  /* background: #555; */
  display: block;
  padding: 7px 0;
  padding-top: 10px;
  color: #fff;
  font-size: 9px;
  font-weight: bold;
  border-bottom: 2px solid #333;
  border-radius: 2px;
  box-shadow: inset 0 -1px 0 0 #666;
}

.date .day {
  display: block;
  margin: 0;
  padding: 10px 0;
  font-size: 20px;
  box-shadow: 0 1px 3px #ccc;
  position: relative;
}

.date .day::after {
  content: '';
  display: block;
  height: 100%;
  width: 96%;
  background: #fff;
  position: absolute;
  top: 3px;
  left: 2%;
  border-radius: 2px;
  z-index: -1;
  box-shadow: 0 0 3px #ccc;
}

.date .day::before {
  content: '';
  display: block;
  height: 100%;
  width: 90%;
  position: absolute;
  background: #fff;
  top: 6px;
  left: 5%;
  z-index: -1;
  box-shadow: 0 0 3px #ccc;
  border-radius: 2px;
}
